.Results-component {
  height: auto;
  width: 100%;
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.Search-Button {
  color: #572EE5;
  font-family: Roboto;
  border-style: none;
  font-size: 24px;
  text-align: center;
}

.Text-info {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.2em;
}

/* ligth mode results component*/
.Results-component.ligth {

  background-color: #e7e7e7;
}

.Text-info.ligth {
  color: #572EE5;
}

/*dark mode results component*/
.Results-component.dark {
  background-color: #212121;
  ;
}

.Text-info.dark {
  color: #fff;
}

/*
Responsive
*/
@media (max-width:768px) {
  .Results-component {
    display: flex;
    height: auto;
    width: 100%;
  }


}